/**************
Variables
**************/
/**************
Base
**************/
a {
  color: #00c; }

/**************
Utilities
**************/
.dash-after:after {
  content: " - "; }

/**************
Search
**************/
@media (min-width: 768px) {
  .results .find-jobs-container {
    text-align: left; }

  .results .find-jobs {
    margin-top: 26px;
    width: auto; } }
@media (min-width: 990px) {
  .home .find-jobs-container {
    text-align: left; }

  .home .find-jobs {
    margin-top: 26px; } }
.find-jobs {
  width: 100%;
  margin-top: 10px; }

.find-jobs-container {
  text-align: center;
  margin-bottom: 15px; }

.find-jobs-container a {
  font-size: 11px; }

.label-main {
  color: #f60;
  font-size: 12px; }

.label-sub {
  color: #aaa;
  font-size: 11px; }

/**************
Home
**************/
.home .container {
  max-width: 800px; }

/**************
Sidebar
**************/
.distance-dropdown #dropdownMenu1 {
  width: 100%; }
.distance-dropdown ul {
  width: 100%; }

#filters {
  margin-top: 15px; }

/**************
Results
**************/
.results .logo img {
  display: block;
  width: 80%;
  margin: 0 auto;
  max-width: 170px; }
.results .container {
  margin-left: 0; }

.result-list {
  border-bottom: 2px solid #f60; }

.results-content {
  margin-top: 15px; }

.result-title {
  font-size: 16px;
  font-weight: 600; }

/**************
Footer
**************/
footer {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%; }


